}
.page {
  font-size: 16px;
  line-height: 1.4;
  color: #292929;
  font-family: cursive;
}
.page::-webkit-scrollbar {
  width: 8px;
}
.page::-webkit-scrollbar-track {
  background-color: #90aff7;
}
.page::-webkit-scrollbar-thumb {
  background-color: #4478F2;
}
.page__root {
  display: flex;
  background-color: #ebebeb;
}
.page__sidebar {
  position: static;
  top: 0;
  min-height: 100vh;
  min-width: 240px;
  height: 100%;
  color: #FFFFFF;
  background-color: #4478F2;
}
.page__sidebar::after {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  width: 8px;
  height: 100%;
  background: linear-gradient(to left, #1e5cef, transparent);
}
.page__content {
  margin: 40px;
  width: 1180px;
}
.button {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 48px;
  border-radius: 4px;
  background-color: #4478F2;
  color: #FFFFFF;
  padding: 0 32px;
  border: 2px solid #4478F2;
  box-shadow: 0 4px 8px #adadad;
  cursor: pointer;
  transition: 0.3s;
}
.button:hover {
  box-shadow: none;
  color: #4478F2;
  background-color: transparent;
}
.form {
  border-radius: 8px;
  background-color: #FFFFFF;
  padding: 40px 56px;
  display: grid;
  grid-template-columns: 340px 340px;
  grid-auto-rows: 48px;
  gap: 32px;
  box-shadow: 0 8px 16px #ebebeb;
}
.form--small {
  grid-template-columns: 340px;
}
.form__element {
  position: relative;
  width: 100%;
  height: 100%;
}
.form__field {
  position: absolute;
  width: 100%;
  height: 100%;
  border: none;
  display: flex;
  align-items: center;
  padding: 0 16px;
  left: 0;
  top: 0;
  font-size: 0;
  border-radius: 4px;
  border: 1px solid #d6d6d6;
  font-size: 18px;
}
.form__field:hover {
  border-color: #292929;
}
.form__field:focus {
  outline: none;
  border: 2px solid #4478F2;
}
.form__field::placeholder {
  transition: 0.3s;
  font-size: 0;
  color: #d6d6d6;
}
.form__field:focus::placeholder {
  font-size: 18px;
}
.form__label {
  position: absolute;
  left: 16px;
  top: 0;
  transform: translateY(-50%);
  font-size: 14px;
  line-height: 1;
  padding: 4px;
  background-color: #FFFFFF;
  color: #adadad;
  transition: 0.3s;
}
.form__field[value=""] + .form__label {
  top: 50%;
  font-size: 18px;
  color: #adadad;
}
.form__field:focus + .form__label {
  top: 0;
  font-size: 14px;
  color: #4478F2;
}
.form__buttons {
  grid-column: 1/-1;
  display: flex;
  justify-content: center;
}
.form__button {
  min-width: 212px;
}
.main-logo {
  display: flex;
  max-width: max-content;
  margin: 32px auto;
}
.main-navigation {
  color: #dde6fd;
}
.main-navigation__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.main-navigation__item {
  position: relative;
  padding: 0 16px;
}
.main-navigation__item:last-child {
  margin-top: 17px;
}
.main-navigation__item:last-child::before {
  content: "";
  position: absolute;
  left: 0;
  top: -9px;
  width: 100%;
  height: 1px;
  background-color: #90aff7;
}
.main-navigation__item--current::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  background-color: #FFFFFF;
  width: 3px;
  border-radius: 3px;
  height: 50%;
  transform: translateY(-50%);
}
.main-navigation__link {
  display: flex;
  align-items: center;
  min-height: 48px;
  width: 100%;
  padding: 0 32px;
  min-width: max-content;
  border: 0;
  background-color: transparent;
  border-radius: 16px;
  cursor: pointer;
}
.main-navigation__link:hover {
  color: #FFFFFF;
}
.main-navigation__item--current .main-navigation__link {
  background-color: #6a94f5;
  color: #FFFFFF;
  cursor: context-menu;
}
.login-screen {
  min-height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
